import React from "react";

const promo = (props) => {
  return (
    <div class="row" style={{ marginTop: "50px" }}>
      {props.gambar.map((data, key) => (
        <div class="col s12 m4" key={key}>
          <div
            class="card"
            style={{
              border: "3px solid red",
              borderRadius: "20px",
              overflow: "hidden",
            }}
          >
            <div class="card-image">
              <img src={data.gambar} alt="gambar" height="250px" />
            </div>
            <div
              class="card-content"
              style={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <p className="blue-text">{data.nama}</p>
              <p
                style={{
                  border: "2px solid black",
                  borderRadius: "10px",
                  padding: "5px 10px 5px 10px",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                {data.harga}
              </p>
              <b className="red-text">Diskon</b>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default promo;
