import React, { Component } from "react";
import Logo from "../component/logo";
import { Link } from "react-router-dom";

class login extends Component {
  render() {
    return (
      <div className="container row">
        <Logo title="Login" />
        <h4
          style={{ marginTop: "100px", textAlign: "center", fontWeight: 600 }}
        >
          Login Akun
        </h4>
        <div className="row">
          <div className="col s3"></div>
          <form className="col s6">
            <div className="row">
              <div className="input-field col s12">
                <i className="material-icons prefix">email</i>
                <input id="icon_prefix" type="email" className="validate" />
                <label for="icon_prefix">Email</label>
              </div>
              <div className="input-field col s12">
                <i className="material-icons prefix">lock</i>
                <input
                  id="icon_telephone"
                  type="password"
                  className="validate"
                />
                <label for="icon_telephone">Password</label>
              </div>
              <div className="input-field col s12">
                <button className="btn blue" style={{ width: "100%" }}>
                  Login
                </button>
                <p className="text-grey">
                  Jika anda belum mempunyai akun Daftarkan akun anda{" "}
                  <Link to="/daftar">Mendaftar</Link>
                </p>
              </div>
            </div>
          </form>
          <div className="col s3"></div>
        </div>
      </div>
    );
  }
}

export default login;
