import React, { Component } from "react";
import Logo from "../component/logo";

class tracker extends Component {
  render() {
    return (
      <>
        <div className="container row">
          <Logo title="Tracker" />
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <h4 style={{ marginTop: "50px" }}>Melacak kiriman anda:</h4>
            <div className="row">
              <div className="col s3"></div>
              <form className="col s12">
                <div className="row">
                  <div className="input-field col s12">
                    <i className="material-icons prefix">place</i>
                    <input
                      id="icon_telephone"
                      type="text"
                      className="validate"
                    />
                    <label for="icon_telephone">
                      Masukkan no invoice anda..
                    </label>
                  </div>
                  <div className="input-field col s12">
                    <button className="btn red" style={{ width: "100%" }}>
                      Lacak Pesanan
                    </button>
                    <p>
                      * Masukkan No Invoice Anda dan Klik Tombol Lacak Pesanan
                      Untuk melacak pesanan Anda
                    </p>
                  </div>
                </div>
              </form>
              <div className="col s3"></div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default tracker;
