import React, { Component } from "react";
import Logo from "../component/logo";
import a from "../assets/gambar/file_1481423289.jpg";
import b from "../assets/gambar/file_1481423323.jpg";
import c from "../assets/gambar/file_1481423391.jpg";
import d from "../assets/gambar/file_1481423407.jpg";
import e from "../assets/gambar/file_1481423428.jpg";
import f from "../assets/gambar/file_1481505660.jpg";
import g from "../assets/gambar/file_1481505718.jpg";
import h from "../assets/gambar/file_1481505737.jpg";
import Produk from "../component/produk";

class produk extends Component {
  constructor(props) {
    super(props);
    this.state = {
      gambar: [
        { gambar: a },
        { gambar: b },
        { gambar: c },
        { gambar: d },
        { gambar: e },
        { gambar: f },
        { gambar: g },
        { gambar: h },
      ],
    };
  }
  render() {
    return (
      <div className="container row">
        <Logo title="Produk Jadi" />
        <Produk gambar={this.state.gambar} />
      </div>
    );
  }
}

export default produk;
