import React from "react";

const produk = (props) => {
  return (
    <div class="row" style={{ marginTop: "50px" }}>
      {props.gambar.map((data, key) => (
        <div class="col s12 m3" key={key}>
          <div class="card">
            <div class="card-image">
              <img src={data.gambar} alt="gambar" height="250px" />
            </div>
            <div class="card-content">
              <b>Nama Produk</b>
            </div>
            <div class="card-action">Rp. 20.000</div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default produk;
