import React, { Component } from "react";
import Logo from "../component/logo";
import { Link } from "react-router-dom";

class daftar extends Component {
  render() {
    return (
      <div className="container row">
        <Logo title="Daftar" />
        <h4
          style={{ marginTop: "100px", textAlign: "center", fontWeight: 600 }}
        >
          Daftar Akun
        </h4>
        <div className="row">
          <div className="col s3"></div>
          <form className="col s6">
            <div className="row">
              <p>NB Tanda ( * ) wajib diisi</p>
              <div className="input-field col s12">
                <i className="material-icons prefix">person</i>
                <input id="nama" type="text" className="validate" />
                <label for="nama">* Nama</label>
              </div>
              <div className="input-field col s12">
                <i className="material-icons prefix">place</i>
                <input id="alamat" type="text" className="validate" />
                <label for="alamat">* Alamat</label>
              </div>
              <div className="input-field col s12">
                <i className="material-icons prefix">phone</i>
                <input id="hp" type="tel" className="validate" />
                <label for="hp">* Nomer HP</label>
              </div>
              <div className="input-field col s12">
                <i className="material-icons prefix">email</i>
                <input id="email" type="email" className="validate" />
                <label for="email">* Email</label>
              </div>
              <div className="input-field col s12">
                <i className="material-icons prefix">lock</i>
                <input id="password" type="password" className="validate" />
                <label for="password">Password</label>
              </div>
              <div className="input-field col s12">
                <i className="material-icons prefix">lock</i>
                <input id="ulangi" type="password" className="validate" />
                <label for="ulangi">Ulangi Password</label>
              </div>
              <div className="input-field col s12">
                <div className="row">
                  <label className="col s6">
                    <input class="with-gap" name="jk" type="radio" />
                    <span>Laki - laki</span>
                  </label>
                  <label className="col s6">
                    <input class="with-gap" name="jk" type="radio" />
                    <span>Perempuan</span>
                  </label>
                </div>
              </div>
              <div class="file-field input-field col s12">
                <div class="btn">
                  <span>Upload Foto</span>
                  <input type="file" />
                </div>
                <div class="file-path-wrapper">
                  <input
                    class="file-path validate"
                    type="text"
                    placeholder="Foto harus berekstensi JPG PNG BMP"
                  />
                </div>
              </div>
              <div className="input-field col s12">
                <button className="btn red" style={{ width: "100%" }}>
                  Daftar
                </button>
                <p className="text-grey">
                  Jika anda sudah mempunyai Akun silahkan{" "}
                  <Link to="/login">Login</Link>
                </p>
              </div>
            </div>
          </form>
          <div className="col s3"></div>
        </div>
      </div>
    );
  }
}

export default daftar;
