import React, { Component } from "react";
import Logo from "../component/logo";
import a from "../assets/gambar/file_1494160941.jpg";
import b from "../assets/gambar/file_1494160974.jpg";
import c from "../assets/gambar/file_1494161073.jpg";
import d from "../assets/gambar/file_1494161100.jpg";
import e from "../assets/gambar/file_1494161133.jpg";
import f from "../assets/gambar/file_1494161185.jpg";
import g from "../assets/gambar/file_1494161206.jpg";
import h from "../assets/gambar/file_1586905026.jpg";
import Produk from "../component/produk";

class manik extends Component {
  constructor(props) {
    super(props);
    this.state = {
      gambar: [
        { gambar: a },
        { gambar: b },
        { gambar: c },
        { gambar: d },
        { gambar: e },
        { gambar: f },
        { gambar: g },
        { gambar: h },
      ],
    };
  }
  render() {
    return (
      <div className="container row">
        <Logo title="Manik - manik" color="grey" />
        <Produk gambar={this.state.gambar} />
      </div>
    );
  }
}

export default manik;
