import React, { Component } from "react";
import Logo from "../component/logo";
import a from "../assets/gambar/file_1588487080.jpg";
import b from "../assets/gambar/file_1588491760.jpg";
import c from "../assets/gambar/file_1588522059.jpg";
import d from "../assets/gambar/file_1588521240.jpg";
import e from "../assets/gambar/file_1588514037.jpg";
import f from "../assets/gambar/file_1588517292.jpg";
import Produk from "../component/promo";

class promo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [
        { gambar: a, nama: "Gelang Cantik", harga: "Rp. 20.000" },
        { gambar: b, nama: "Gelang Hitam", harga: "Rp. 15.000" },
        { gambar: c, nama: "Gelang Manik", harga: "Rp. 18.000" },
        { gambar: d, nama: "Gelang Batu", harga: "Rp. 25.000" },
        { gambar: e, nama: "Gelang Warna", harga: "Rp. 14.000" },
        { gambar: f, nama: "Gelang Merah", harga: "Rp. 30.000" },
      ],
    };
  }
  render() {
    return (
      <div className="container row">
        <Logo title="Hot Promo" color="red" />
        <Produk gambar={this.state.data} />
      </div>
    );
  }
}

export default promo;
