import React, { Component } from "react";
import Logo from "../component/logo";
import { Link } from "react-router-dom";

class keranjang extends Component {
  render() {
    return (
      <div className="container row">
        <Logo title="Keranjang Belanja" />
        <table
          className="centered striped responsive-table"
          style={{ marginTop: "50px", marginBottom: "50px" }}
        >
          <thead className="blue">
            <tr>
              <th>Nama Barang</th>
              <th>Jumlah</th>
              <th>Subtotal</th>
              <th>Aksi</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
          </tbody>
          <tfoot className="blue">
            <td colSpan="2">Total</td>
            <td colSpan="2"></td>
          </tfoot>
        </table>
        <div
          style={{
            marginTop: "50px",
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <button className="btn blue">Update Keranjang</button>
          <button className="btn red">Cek Ongkos Kirim</button>
          <button className="btn blue">Checkout</button>
        </div>
        <p
          className="text-grey"
          style={{ marginTop: "50px", marginBottom: "50px" }}
        >
          Jika jumlah barang yang Anda inginkan lebih dari satu Tambahkan jumlah
          barang sesuai dengan yang Anda inginkan dan klik tombol update
          keranjang jika masih ada lagi barang ang akan Anda beli selain diatas
          Klik &nbsp;
          <b>
            <u>
              <Link to="/barang">disini</Link>
            </u>
          </b>
          &nbsp; untuk memilih produk lainnya
        </p>
      </div>
    );
  }
}

export default keranjang;
